import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import PrimaryRune from './PrimaryRune';
import SecondaryRune from './SecondaryRune';
import StatMods from './StatMods';

function RuneBuild({ primaryRuneProp, primarySelectionsProp, secondaryRuneProp, secondarySelectionsProp, statModsProp }) {
    // useEffect(() => {
    //     const games = data['selected_runes']['build'].reduce((acc, val) => val.games + acc, 0);
    //     const wins = data['selected_runes']['build'].reduce((acc, val) => val.wins + acc, 0);
    //     setWinRate(Math.floor(wins / games * 10000) / 100);
    //     setMatches(games);
    // }, [data]);

    return (
        <>
            <Container>
                {/* <div className="stats">
                    <h5 className={`winrate ${parseFloat(winRate) >= 50.0 ? "high" : "low"}`}>{winRate}% WR</h5>
                    <h5 className="matches">({matches} matches)</h5>
                </div> */}
                <div className="runes-container">
                    <PrimaryRune primaryRuneProp={primaryRuneProp} primarySelectionsProp={primarySelectionsProp}/>
                    <div className="secondary-runes-container">
                        <SecondaryRune secondaryRuneProp={secondaryRuneProp} secondarySelectionsProp={secondarySelectionsProp}/>
                        <StatMods statModsProp={statModsProp} />
                    </div>
                </div>
            </Container>
        </>
    )
}

const Container = styled.div`

    .stats {
        display: flex;
        align-items: flex-end;
        gap: 1.5rem;
        padding-bottom: 1rem;

        h5 {
            margin: 0rem;
            padding: 0rem;
        }

        .matches {
            color: #BF905B;
        }
        
        .high {
            color: green;
        }

        .low {
            color: red;
        }
    }

    .secondary-runes-container {
        display: flex;
        flex-direction: column;
        align-items: space-between;

    }

    .runes-container {
        display: flex;
        justify-content: start-flex;
        gap: 5rem;

    }
`;

export default RuneBuild;