import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import axios from 'axios';

function SecondaryRune({secondaryRuneProp, secondarySelectionsProp}) {
    const iconBaseURL = 'https://ddragon.leagueoflegends.com/cdn/img/';
    const [styleString, setStyleString] = useState('N/A');
    const [runes, setRunes] = useState(undefined);
    const [styleIcon, setStyleIcon] = useState(undefined);
    const [selectedRunes, setSelectedRunes] = useState(undefined);
    const [secondaryRune, setSecondaryRune] = useState(undefined);


    // Set states for picked runes
    useEffect(() => {
        if (secondarySelectionsProp) {
            setSecondaryRune(secondaryRuneProp);
            setSelectedRunes(secondarySelectionsProp);
        }
    }, [secondarySelectionsProp]);

    // Get the list of runes
    useEffect(() => {
        async function fetchRunes() {
            const URL = 'https://ddragon.leagueoflegends.com/cdn/12.6.1/data/en_US/runesReforged.json';
            const { data } = await axios.get(URL);
            const style = data.find(x => x.id === secondaryRune.secondaryStyle);
            setStyleIcon(style['icon']);
            setRunes(style['slots']);
            setStyleString(style['key']);
        }
        if (secondaryRune) {
            fetchRunes();
        }
    }, [secondaryRune]);

    return (
        <>
            <Container>
                <div className="header">
                    <span className="block"></span>
                    <img src={`${iconBaseURL}${styleIcon}`} alt="" />
                    <div className="h5">{styleString}</div>
                </div>
                <div className="selected-runes">
                    <div className="rune-container">
                        {runes && (runes.slice(1,4).map((slots, index) => {
                            return (
                                <div 
                                className="slot"
                                id={index}
                                key={index}
                                >
                                    {slots['runes'].map((rune, index) => {
                                        return <img
                                        className={`${secondarySelectionsProp.picks.match(/.{1,4}/g).includes(`${rune.id}`) ? 'selected': ''}`}
                                        src={`${iconBaseURL}${rune['icon']}`} 
                                        alt="" 
                                        id={rune['id']}
                                        key={index}
                                        />
                                    })}
                                </div>
                            )
                        }))}
                    </div>
                </div>
            </Container>
        </>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 15rem;

    .selected-runes {
        width: 100%;
    }

    .header {
        display: flex;
        justify-content: flex-start;

        img {
            height: 2.5rem;
            width: 2.5rem;
        }
    }

    .block {
        background: white;
        padding: 0.5rem 0rem;
        width: 0.2rem;

        &::before {
            content: "";
            white-space: pre;
        }
    }

    .slot:last-child {
        // background-color: white;
        padding-bottom: 0.5rem;
        margin-bottom: 0.5rem;
        border-bottom: 1px solid white;
    }

    .slot {
        display: flex;
        padding-top: 1rem;
        justify-content: space-between;
        gap: 1rem;
        
        .selected {
            filter: none !important;
            border: 0.2rem solid #309DDA;
            border-radius: 3rem;
        }

        img {
            width: 2.3rem;
            height: 2.3rem;
            filter: grayscale(0.9);
            border: 0.2rem solid transparent;
        }
    }
`;

export default SecondaryRune;