import { React, useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import styled from 'styled-components'
import { getBuildByIdRoute, getBuildByNameRoute } from '../utils/APIRoutes';
import axios from 'axios';
import ItemBuild from './ItemBuild';
import SkillGrid from './SkillGrid';
import RuneBuild from './RuneBuild';
import Home from './Home';


function Build() {
    const [championName, setChampionName] = useState(undefined);
    const [championId, setChampionId] = useState(undefined);
    const [role, setRole] = useState(undefined);
    const [data, setData] = useState(undefined);

    // Get the query parameters of the route
    const search = useLocation().search;
    const roleExists = new URLSearchParams(search).get('role');
    const championIdExists = new URLSearchParams(search).get('championId');
    const championNameExists = new URLSearchParams(search).get('championName');

    // Fetch champion build data
    useEffect(() => {
        if (roleExists && championIdExists) {
            setRole(roleExists);
            setChampionId(championIdExists);
        }
        if (roleExists && championNameExists) {
            setRole(roleExists);
            setChampionName(championNameExists);
        }
    }, [roleExists, championIdExists]);

    useEffect(() => {
        async function fetchChampionBuildById() {
            const { data } = await axios.get(`${getBuildByIdRoute}${championId}?lane=${role}`);
            if (data) {
                console.log(data.data);
                setData(data.data);
            }
        };
        async function fetchChampionBuildByName() {
            const res = await axios.get(`${getBuildByNameRoute}?championName=${championName}&role=${role}`);
            if (res.data.status) {
                setData(res.data);
            }
        };
        if (championName) {
            fetchChampionBuildByName();
        } else if (championId) {
            fetchChampionBuildById();
        }
    }, [role, championId, championName]);

    return (
        <>  
            <Home />
            {data == undefined 
                ?
                <Container>
                    Not enough data
                </Container>
                :
                <Container>
                    <div>
                        <h3>{data.matches} matches</h3>
                        <h3>{data.wins} wins</h3>
                    </div>
                    <ItemBuild 
                        startingItemsProp={data.startingItems} 
                        mythicProp={data.mythic} 
                        bootsProp={data.boots} 
                        secondProp={data.second}
                        thirdProp={data.third}
                        fourthProp={data.fourth}
                        fifthProp={data.fifth}
                    />
                    <SkillGrid championIdProp={data.championId} skillPathProp={data.skillPath} propSkillImages={["NocturneDuskbringer.png", "NocturneShroudofDarkness.png", "NocturneUnspeakableHorror.png", "NocturneParanoia.png"]} />
                    <div className="runes-container">
                        <RuneBuild 
                            primaryRuneProp={data.primaryRune} 
                            primarySelectionsProp={data.primarySelections} 
                            secondaryRuneProp={data.secondaryRune} 
                            secondarySelectionsProp={data.secondarySelections} 
                            statModsProp ={data.stats}
                        />
                    </div>
            </Container>
            }
            
        </>
    )

}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 3rem;
    
    .secondary-runes-container {
        display: flex;
        flex-direction: column;
        align-items: space-between;

    }

    .runes-container {
        display: flex;
        justify-content: start-flex;
        gap: 5rem;

    }
`;

export default Build;