champions.sort((a, b) => a.label.localeCompare(b.label));

const Home = () => {
    const [search, setSearch] = useState('');
    const navigate = useNavigate();

    const getBuild = (championId, lane) => {
        navigate(`/champion/build?championId=${championId}&role=${lane}`);
    }

    return (
    <div>
        <h1 onClick={() => navigate('/')}>LeagueOne</h1>
        <Stack
            direction='row'
        >
            <Autocomplete 
                id='champion-select'
                sx={{ width: 300 }}
                size='normal'
                options={champions}
                autoHighlight
                autoComplete
                autoSelect
                onChange={(e, value) => setSearch(value.id)}
                renderInput={(params) => (
                    <TextField 
                        variant='filled'
                        sx={{ background: 'white' }}
                        {...params}
                        label='Search champion'
                    />
                )}
            />
            <ButtonGroup variant="outlined" aria-label="outlined button group">
                <Button onClick={() => getBuild(search, 'top')} className='lane-button'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><g fill="none" fillRule="evenodd"><path fill="#565691" d="M5.867 10.133h4.266V5.867H5.867z"></path><path fill="#565691" d="M16 16V1.25L13.172 4.08v9.093H4.078L1.25 16z"></path><path fill="#FFF" d="M0 0v14.75l2.828-2.829V2.828h9.094L14.75 0z"></path></g></svg></Button>
                <Button onClick={() => getBuild(search, 'jungle')} className='lane-button'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path d="M3.078 0s8.57 8.931 5.13 16c0 0-2.414-3.123-5.31-4.548 0 0 .482-4.22-2.898-7.014 0 0 4.587 1.973 5.553 5.041 0 0 1.086-4.383-2.475-9.479zM16 4s-3.44 3.068-2.837 6.85c0 0-2.414 1.424-2.836 2.3 0 0 .241-6.63 5.673-9.15zm-3.393-4s-2.656 4.603-2.414 8c0 0-.543.767-.725 1.425 0 0-.663-2.52-1.207-3.124l.016-.03c.202-.386 2.32-4.395 4.33-6.271z" fill="#FFF" fillRule="evenodd"></path></svg></Button>
                <Button onClick={() => getBuild(search, 'middle')} className='lane-button'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><g fill="none" fillRule="evenodd"><path fill="#565691" d="M8.305 2.828L11.133 0H0v11.133l2.828-2.828V2.828zM7.695 13.172L4.867 16H16V4.867l-2.828 2.828v5.477z"></path><path fill="#FFF" d="M13.371 0L0 13.371v2.63h2.629L16 2.628V0z"></path></g></svg></Button>
                <Button onClick={() => getBuild(search, 'bottom')} className='lane-button'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><g fill="none" fillRule="evenodd"><path fill="#565691" d="M10.133 5.867H5.867v4.266h4.266z"></path><path fill="#565691" d="M0 0v14.75l2.828-2.829V2.828h9.094L14.75 0z"></path><path fill="#FFF" d="M16 16V1.25L13.172 4.08v9.093H4.078L1.25 16z"></path></g></svg></Button>
                <Button onClick={() => getBuild(search, 'support')} className='lane-button'><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path d="M8.43 5.67l1.703 8.616L8 16l-2.133-1.714L7.57 5.67 8 6.249l.43-.578zM16 3.404c-.304 2.498-4.119 2.261-4.119 2.261l1.75 2.315-2.81 1.13L9.6 5.234l1.857-1.83zm-11.457 0L6.4 5.233 5.18 9.11 2.368 7.98l1.75-2.316S.305 5.901 0 3.403h4.543zM9.998 0l.669 1.185L8 4.456 5.333 1.185 6.003 0h3.995z" fill="#FFF" fillRule="evenodd"></path></svg></Button>
            </ButtonGroup>
        </Stack>
        <h5>Get the most up-to-date builds</h5>
    </div>
    )
}
export default Home