import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import axios from 'axios';

function PrimaryRune({statModsProp}) {
    const iconBaseURL = 'https://ddragon.leagueoflegends.com/cdn/img/perk-images/StatMods/';
    const statMods = {
        5005: 'StatModsAttackSpeedIcon.png',
        5008: 'StatModsAdaptiveForceIcon.png',
        5002: 'StatModsArmorIcon.png',
        5007: 'StatModsCDRScalingIcon.png',
        5003: 'StatModsMagicResIcon.png',
        5001: 'StatModsHealthScalingIcon.png'
    };
    const statModsOrder = [[5008, 5005, 5007], [5008, 5002, 5003], [5001, 5002, 5003]];

    const [selectedMods, setSelectedMods] = useState(undefined);

    // Filter runes to only statmods
    useEffect(() => {
        function filterStatMods() {
            if (statModsProp) {
                setSelectedMods(statModsProp);
            }
            // .picks.match(/.{1,4}/g)
        }
        filterStatMods();
    }, [statModsProp]);

    return (
        <>
        {selectedMods && (
            <Container>
                <div className="statmod-container">
                    {statModsOrder.map((row, index) => {
                        return (
                            <div className="statmod-row" id={index}>
                                {row.map((statmod, statindex) => {
                                    return (
                                        <div className="stat-icon" id={statindex}>
                                            <img 
                                                src={`${iconBaseURL}${statMods[statmod]}`}
                                                id={statmod}
                                                className={`${selectedMods.picks.match(/.{1,4}/g)[index] === `${statmod}` ? 'selected' : ''}`}
                                            />
                                        </div>
                                    )
                                })}
                            </div>
                        )
                    })}
                </div>
            </Container>
        )}
        </>
    )
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 15rem;

    .statmod-container {
       width: 100%;
    }

    .statmod-row {
        padding-top: 0.5rem;
        display: flex;
        justify-content: space-between;
        gap: 3rem;

        img {
            width: 1rem;
            height: 1rem;
            background-color: #2B2D65;
            border-radius: 1.5rem;
            border: 0.2rem solid transparent;
            filter: grayscale(0.9);
        }

        .selected {
            filter: none !important;
            border: 0.2rem solid #309DDA;
            border-radius: 1.5rem;
        }
    }
`;

export default PrimaryRune;