import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import axios from 'axios';

/**
 * Skill priority component
 * 
 * @param {[Number]} propSkillPriority Array of integers from 1-4 representing skill level up at each index + 1 level
 * @param {[String]} propSkillImages Array of strings in order representing the images of each skill [Q>W>E>R]
 * @param {String} propWinRate String representing the percentage winrate of champion
 * @param {Number} propMatches Number representing the number of games analyzed
 * @returns A component for skill priority
 */

function SkillGrid({skillPathProp, championIdProp}) {
    const [skillPriority, setSkillPriority] = useState([]);
    const [skillImages, setSkillImages] = useState([]);
    const baseSkillURL = 'https://ddragon.leagueoflegends.com/cdn/12.6.1/data/en_US/championFull.json';

    useEffect(() => {
        if (skillPathProp) {
            setSkillPriority(skillPathProp);
        }
    }, [skillPathProp]);

    // Get & Set champion spell icons
    useEffect(() => {
        async function fetchChampionSkills() {
            const { data: respData } = await axios.get(baseSkillURL);
            const { data: championData, keys: championKeys } = respData;
            const championKey = championKeys[championIdProp];
            const spells = championData[championKey]["spells"].map((spell) => spell["image"]["full"]);

            setSkillImages(spells);
        }
        if (championIdProp) {
            fetchChampionSkills();
        }
    }, [championIdProp]);

    // console.log(skillPriority);

    return (
        <>
            <Container>
                    <div className="wrapper">
                        <div className="header">
                            <div className="title">
                                <h4>Skill Priority</h4>
                            </div>
                        </div>
                        
                    </div>
                    <div className="wrapper">
                        <div className="skill-col">
                            {skillImages.map((skill, index) => {
                                const skillKeys = ["Q", "W", "E", "R"];
                                return (
                                <div 
                                    className="box"
                                    id={index}
                                >
                                    <img src={`https://ddragon.leagueoflegends.com/cdn/12.6.1/img/spell/${skill}`}/>
                                    <div className="skill-button-container">
                                        <div className="skill-button">{skillKeys[index]}</div>
                                    </div>
                                </div>
                                )
                            })}
                        </div>

                        {skillPriority.map((skill, index) => {
                            return (
                                <div 
                                    className="skill-col"
                                    key={index}
                                >
                                    {new Array(4).fill(null).map((val, idx) => idx + 1).map((num, index) => {
                                        return (<div id={num} className={`box${skill === num ? " selected" : ""}`} key={index}>{`${skill === num ? index + 1 : ""}`}</div>)
                                    })}
                                </div>
                            )
                        })}
                    </div>
            </Container>
        </>
    );

}

const Container = styled.div`
    display: flex;
    flex-direction: column;

    .wrapper {
        display: flex;
        justify-content: space-between;
    }

    .header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 1rem;
        width: 100%;

        .title {
            display: flex;
            align-items: flex-end;
            font-size: 1.5rem;
            font-weight: bold;

            h4 {
                margin: 0rem;
                padding: 0rem;
            }
        }   
        
        .stats {
            display: flex;
            align-items: flex-end;
            gap: 1.5rem;

            h5 {
                margin: 0rem;
                padding: 0rem;
            }

            .matches {
                color: #BF905B;
            }
            
            .high {
                color: green;
            }

            .low {
                color: red;
            }
        }
    }

    
    .skill-col {
        margin-right: 2rem;
        display: grid;
        gap: 0.5rem;
        grid-template-columns: 0.5rem;

        .box {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #2B2D65;
            width: 2rem;
            height: 2rem;
            color: black;
            font-size: 18px;
            font-weight: bold;
            border-radius: 0.2em;
            overflow: hidden;
            transition: transform .1s ease-in-out;

            &:hover {
                cursor: pointer;
                transform: scale(1.5);
            }

            img {
                width: 100%;
            }

            .skill-button-container {
                display: flex;
                align-items: flex-start;
                justify-content: flex-start;
                position: absolute;
                width: 3rem;
                height: 3rem;

                .skill-button {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    color: white;
                    background-color: #2B2D65;
                    width: 1rem;
                    height: 1rem;
                    font-size: 0.5rem;
                    border-radius: 0.2rem;
                    border: 0.1rem white solid;
                }
            }

            
        }

        .selected {
            background-color: #309DDA;
        }
    }
`;

export default SkillGrid;